body {
 font-family: 'Roboto', sans-serif;

}

.logo{
  width:100px;
}

.altura-a-b{
  
  padding: 5% 0;
}

.container {
  img{
  transition: all 300ms;
  &:hover{transform: scale(1.1);
  cursor: pointer;

  }

  }
  
}

.coronavirus{
  width: 100%;
    height: auto;
    @include colores;
    padding: 20px;
}

footer {
  background-color: #000000;
  padding: 100px 0;
  text-align: $t-center;
  @include letra;
}

.fondo {
  background-image: url(../imagenes/8.jpg);
  height: 50vh;
  background-repeat: no-repeat;
  background-position:$bg-position ;
  background-size: cover;
  background-attachment: fixed;
}

.fondo2 {
  background-image: url(../imagenes/Call-Center.jpg);
  height: 50vh;
  background-repeat: no-repeat;
  background-position: $bg-position;
  background-size: cover;
  background-attachment: fixed;
}

.bg{
  background-image: url(../imagenes/mensajeria.png);
  background-repeat: no-repeat;
  background-position: $bg-position;
}

.card-img-overlay {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    padding: 150px;
    border-radius: calc(0.25rem - 1px);
    text-align: center;
    color:#fff;




}

.container2{
  background-color:blue;
  padding:25px;
  border-radius: 80px;
}

.carousel-item {
  max-height:500px;
  
}
